import React, {Component} from "react";
import {Button, Form,Col,InputGroup,FormControl} from "react-bootstrap";
import './OrderForm.css'

class OrderForm extends Component{
    state={
        firstName:'',
        lastName:'',
        PhoneNumber:'',
        Reason:'',
        items:[""],
    }
    addItem=()=>{
        console.log("sdasd")
         let copiedItems=this.state.items
         copiedItems.push('')
         this.setState({items:copiedItems})
        // this.setState(prevState => ({ items: [...prevState.items, '']}))
        console.log(this.state.items)
    }
    render() {
        return(
            <div>
                <Form>
                    <Form.Group controlId="formBasicEmail">
                        <Form.Label>First name</Form.Label>
                        <Form.Control type="text" placeholder="Enter name" />
                    </Form.Group>
                    <Form.Group controlId="formBasicEmail">
                        <Form.Label>Last name</Form.Label>
                        <Form.Control type="text" placeholder="Enter last name" />
                    </Form.Group>
                    <Form.Group controlId="formBasicEmail">
                        <Form.Label>Phone number</Form.Label>
                        <Form.Control type="text" placeholder="Enter phone number" />
                    </Form.Group>
                    <Form.Group controlId="exampleForm.SelectCustom">
                        <Form.Label>Reason</Form.Label>
                        <Form.Control as="select" custom>
                            <option>In isolation</option>
                            <option>In risk group</option>
                            <option>other</option>
                        </Form.Control>
                    </Form.Group>
                    <Form.Group>
                        <Form.Label>groceries</Form.Label>
                    {
                        this.state.items.map((item,i)=>(
                            <div key={i}>
                                <Form.Row>
                                    <InputGroup>
                                        <FormControl
                                            placeholder="Item"
                                            aria-label="Item"
                                            aria-describedby="basic-addon2"
                                        />
                                        <div className='orderButton'>
                                            <InputGroup.Append>
                                                <Button variant="outline-secondary" onClick={this.addItem}>Add</Button>
                                                <Button variant="outline-secondary">Remove</Button>
                                            </InputGroup.Append>
                                        </div>

                                    </InputGroup>
                                </Form.Row>
                            </div>
                        ))
                    }
                    </Form.Group>

                        <Button variant="primary" type="submit">
                            Submit
                        </Button>
                </Form>
            </div>
        )
    }
}
export default OrderForm